.header {
  background: #040c20;
  border-radius: 20px;
  position: fixed;
  top: 20px;
  box-shadow: 0 4px 10px rgba(9, 9, 9, .61);
}

.header__list {
  width: 660px;
  gap: 60px;
  display: flex;
}

.header__item {
  list-style: none;
}

.header__link {
  color: #cebdbd;
  padding: 5px;
  font-size: 24px;
  font-weight: bolder;
  text-decoration: none;
}

.header__link:hover, .header__link:active {
  background-color: #746e6e;
  border-radius: 20px;
}

.home {
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.home__header {
  font-size: 40px;
  font-weight: bolder;
}

.home__avatar {
  width: 100px;
  border: 3px solid #cebdbd;
  border-radius: 50%;
}

.home__description {
  text-align: center;
  margin-right: 10px;
  padding-left: 10px;
  font-size: 20px;
  font-weight: bolder;
}

body {
  box-sizing: border-box;
  color: #fff;
  width: 700px;
  background-color: #202128;
  margin: 0 auto;
  padding: 0;
}

